.title_portfolio{text-align:center;margin-bottom:30px;z-index:7;display:inline-block;font-size:3.5rem;padding:15px 30px;border-radius:10px;font-weight:900;letter-spacing:4px;border:3px solid #d4af37;box-shadow:0 6px 20px rgba(0,0,0,.3),0 0 30px rgba(212,175,55,.5),inset 0 2px 10px rgba(244,208,63,.3);position:relative;overflow:hidden}.title_portfolio::before{content:"ð“‚€";position:absolute;left:10px;top:50%;transform:translateY(-50%);font-size:1.5rem;opacity:.6}.title_portfolio::after{content:"ð“‚€";position:absolute;right:10px;top:50%;transform:translateY(-50%);font-size:1.5rem;opacity:.6}.conteneur-titre{text-align:center}.title_char{display:inline-block;animation:jump .5s ease-in-out infinite}.conteneur-portfolio{border-radius:32px;padding:5%;top:30px;margin:0 auto 80px;box-sizing:border-box;width:90%;position:relative;background:rgba(26,26,46,.4);border:1px solid rgba(201,168,88,.3);box-shadow:0 16px 64px rgba(0,0,0,.4),0 0 100px rgba(201,168,88,.2),inset 0 1px 0 hsla(0,0%,100%,.1),inset 0 -1px 0 rgba(201,168,88,.2)}.conteneur-portfolio::before{content:"";position:absolute;top:-2px;right:-2px;bottom:-2px;left:-2px;border-radius:32px;padding:2px;background:linear-gradient(135deg, rgba(201, 168, 88, 0.4), rgba(232, 201, 137, 0.2), rgba(201, 168, 88, 0.4));-webkit-mask:linear-gradient(#fff 0 0) content-box,linear-gradient(#fff 0 0);-webkit-mask-composite:xor;mask:linear-gradient(#fff 0 0) content-box,linear-gradient(#fff 0 0);mask-composite:exclude;opacity:.6}.conteneur-portfolio::after{content:"";position:absolute;top:0;right:0;bottom:0;left:0;border-radius:32px;background:radial-gradient(circle at 50% 0%, rgba(201, 168, 88, 0.1), transparent 60%);pointer-events:none}.filtres{display:flex;justify-content:center;margin-bottom:20px}.filtres button{margin:0 10px;padding:12px 28px;border:none;border-radius:12px;cursor:pointer;font-size:1.1rem;margin-bottom:30px;font-weight:600;letter-spacing:1.5px;position:relative;overflow:hidden;background:rgba(26,26,46,.6);color:gold;box-shadow:0 4px 16px rgba(0,0,0,.3),inset 0 1px 0 hsla(0,0%,100%,.1),0 0 0 1px rgba(201,168,88,.2)}.filtres button::before{content:"";position:absolute;top:0;right:0;bottom:0;left:0;background:linear-gradient(135deg, rgba(201, 168, 88, 0.2), rgba(232, 201, 137, 0.3), rgba(201, 168, 88, 0.2));opacity:0;transition:opacity .4s ease}.filtres button::after{content:"";position:absolute;top:50%;left:50%;width:0;height:0;border-radius:50%;background:radial-gradient(circle, rgba(232, 201, 137, 0.4), transparent);transform:translate(-50%, -50%);transition:width .6s ease,height .6s ease}.filtres button:hover{background:rgba(201,168,88,.3);border:1px solid rgba(201,168,88,.5)}.filtres button:active{background:rgba(201,168,88,.4)}.filtres button.active{background:linear-gradient(135deg, #b8860b 0%, #d4af37 15%, #f4d03f 30%, #ffd700 50%, #f4d03f 70%, #d4af37 85%, #b8860b 100%);box-shadow:0 6px 20px rgba(184,134,11,.5),inset 0 2px 4px hsla(0,0%,100%,.3),inset 0 -2px 4px rgba(139,101,8,.5);color:#1a1a2e;font-weight:700;box-shadow:0 6px 20px rgba(201,168,88,.5),inset 0 1px 0 hsla(0,0%,100%,.3),0 0 0 1px rgba(201,168,88,.6)}.grid_projets{display:grid;grid-template-columns:repeat(3, 1fr);gap:32px;margin-left:0px;padding:20px 0}.grid_projets>div{width:100%}@media(min-width: 768px)and (max-width: 1023.9px){.conteneur-portfolio{width:95%;padding:4%;border-radius:28px}.title_portfolio{font-size:2.8rem;padding:12px 25px;letter-spacing:3px}.title_portfolio::before,.title_portfolio::after{font-size:1.3rem}.filtres{flex-wrap:wrap;gap:10px;margin-bottom:25px}.filtres button{font-size:1rem;padding:10px 20px;margin:5px}.grid_projets{grid-template-columns:repeat(2, 1fr);gap:25px;padding:15px 0}}@media(min-width: 320px)and (max-width: 767.9px){.conteneur-portfolio{width:98%;padding:20px 10px;margin-bottom:50px;border-radius:24px}.conteneur-titre{margin-bottom:15px}.title_portfolio{font-size:1.4rem;margin-top:10px;margin-bottom:5px;padding:8px 12px;letter-spacing:1.5px;border:2px solid #d4af37}.title_portfolio::before{left:5px;font-size:1rem}.title_portfolio::after{right:5px;font-size:1rem}.filtres{margin-top:15px;margin-bottom:20px;display:grid;grid-template-columns:repeat(2, 1fr);grid-template-rows:auto;gap:8px;padding:0 5px;justify-items:stretch;align-items:stretch}.filtres button{padding:8px 12px;font-size:.85rem;margin:0;letter-spacing:.5px;width:100%;white-space:nowrap}.filtres button:last-child{grid-column:span 2;justify-self:center;width:60%;min-width:120px}.grid_projets{display:flex;flex-direction:column;align-items:center;gap:20px;padding:10px 5px;width:100%}}